import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Select from 'react-select';
import BuilderActions from './action/builderAction';
import clone from 'clone';
import { findIndex } from 'underscore';
import KeyStore from './store/keyStore';
import { Accordion, Panel } from 'react-bootstrap';
import ReactiveEditPanel from './editPanel';

class ReactiveAdminComponent extends Component {
  constructor(props) {
    super(props);
    this.selectedOptions = [];
    this.handleChange = this.handleChange.bind(this);
    this.postValues = [];

    let reactiveData = [];
    if (typeof REACTIVE_DATA !== undefined) {
      reactiveData = clone(REACTIVE_DATA);
    }
    const self = this;
    reactiveData.forEach((item) => {
      self.selectedOptions.push({
        label: item.post_type,
        value: item.post_type,
      });
    });
    this.state = {
      PostTypes: REACTIVE_ADMIN.helper.all_post_types ? REACTIVE_ADMIN.helper.all_post_types : [],
      buildData: reactiveData,
      selectedPostType: {},
    };
  }

  componentDidMount() {
    KeyStore.listen(this.updateBuildData.bind(this));
  }

  updateBuildData(buildData) {
    this.setState({ buildData, selectedPostType: '' });
  }

  handleChange(event) {
    if (event.target.value !== '') {
      this.setState({
        selectedPostType: event.target.value,
      });
    }
  }

  saveData(e) {
    e.preventDefault();
    const { buildData, selectedPostType } = this.state;
    if (selectedPostType !== '') {
      buildData.push({
        post_type: selectedPostType,
      });
    }
    BuilderActions.saveAllData({ allData: buildData });
  }
  removeField(item, event) {
    event.preventDefault();
    const { buildData } = this.state;
    const Index = findIndex(buildData, { post_type: item.post_type });
    buildData.splice(Index, 1);
    BuilderActions.saveAllData({ allData: buildData });
  }

  render() {
    const { buildData, PostTypes, selectedPostType } = this.state;
    const showBuildData = (item, index) => {
      return (
        <Panel header={item.post_type} key={index} eventKey={index}>
          <a href="#" onClick={this.removeField.bind(this, item)} style={{ float: 'right' }}>Remove</a>
          { <ReactiveEditPanel {...item} buildData={buildData} /> }
        </Panel>
      );
    };

    buildData.map((item) => {
      const postTypeIndex = PostTypes.indexOf(item.post_type);
      if (postTypeIndex !== -1) {
        PostTypes.splice(postTypeIndex, 1);
      }
    });

    const showAllOption = PostTypes.map((item, index) => {
      return <option value={item} key={index}>{item}</option>;
    });

    return (
      <div className="reactive-container">
        <h3>{REACTIVE_ADMIN.LANG.PLEASE_SELECT_POST_TYPE_FOR_DATA_RESTRICTION}</h3>

        <div className="col-md-6 reactive-select">
          <select className="form-control" value={selectedPostType} onChange={this.handleChange}>
            <option value="">Please select</option> {showAllOption}
          </select>
        </div>
        <button className="btn btn-add-post" onClick={this.saveData.bind(this)}><i className="icon-layers icons font-bold"></i> {REACTIVE_ADMIN.LANG.SAVE}</button>
        <div className="clearfix"></div>

        <p>{REACTIVE_ADMIN.LANG.ADMIN_ALERT_MESSAGE}</p>
        <Accordion>
          { buildData.length ? buildData.map(showBuildData) : <div>
            <h2 style={{ color: '#b1b1b1', textAlign: 'center' }}> {REACTIVE_ADMIN.LANG.PLEASE_SELECT_A_POST_TYPE} </h2>
          </div>}
        </Accordion>
      </div>
    );
  }
}

if (document.getElementById('reactive-admin')) {
  ReactDOM.render(<ReactiveAdminComponent />, document.getElementById('reactive-admin'));
}



